.hero {
  &__btn {
    margin-top: auto;
    padding: 1rem 2rem;

    max-width: 10.9375rem;

    font-weight: 400;
    font-size: 1rem;
    line-height: 150%;
    color: var(--dark-primary);

    @media (max-width: 34.375rem) {
      max-width: 100%;
    }
  }


}

.hero-homepage {
  min-height: 44rem;
  background-color: var(--white);
  background-image: url("../img/hero.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  @media (max-width: 34.375rem) {
    margin-top: 40px;
    min-height: 0;

    background-image: none;
  }

  &__container {
    padding: 8.125rem 0.9375rem;

    display: flex;
    justify-content: right;

    @media (max-width: 56.25rem) {
      justify-content: center;
    }

    @media (max-width: 34.375rem) {
      padding: 0 1.5625rem;
    }
  }

  &__wrapper {
    padding: 2.8125rem 3.4375rem;

    max-width: 39.375rem;
    min-height: 27.8125rem;

    display: flex;
    flex-direction: column;

    background-color: var(--white);

    @media (max-width: 34.375rem) {
      padding: 0;

      min-height: 0;

      gap: 2rem;
    }
  }

  &__wrapper--header {
    max-width: 31.875rem;

    display: flex;
    flex-direction: column;
    row-gap: 1.25rem;
  }

  &__image {
    margin-top: 2rem;
  }
}

.wrapper {
  &__title {
    margin: 0;

    font-weight: 400;
    font-size: clamp(1.5rem, 1.281rem + 0.899vw, 2rem);
    line-height: 140%;
    color: #22202e;
  }

  &__descr {
    margin: 0;

    font-weight: 400;
    font-size: clamp(1rem, 0.945rem + 0.225vw, 1.125rem);
    line-height: 150%;
    color: #5b5676;
  }
}
