.navbar {
  &__list--main {
    padding: 1.25rem 0;
    display: flex;
    gap: 2.75rem;

    @media (max-width: 51.875rem) {
      gap: 2.1875rem;
    }

    @media (max-width: 44.0625rem) {
      padding: 1.25rem;
      flex-direction: column;
    }
  }

  &__item--main {
  }

  &__link--main {
  }
}

.navbar-main {
  @media (max-width: 44.0625rem) {
    position: fixed;
    top: 0;
    left: -100%;
    overflow-y: auto;
    z-index: 100;
    width: 80%;
    max-width: 23.4375rem;
    height: 100vh;
    background-color: var(--white);
    transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
    box-shadow: 0.625rem 0 1.4375rem -0.3125rem rgba(34, 60, 80, 0.2);
  }
}

.menu--active {
  left: 0;
}
