.header__notification {
  padding: 11px var(--container-offset);

  position: relative;

  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--dark-primary);

  @media (max-width: 500px) {
    justify-content: start;
  }
}

.header__notification-text {
  padding-left: 8px;

  font-weight: 400;
  font-size: 14px;
  color: var(--white);

  @media (max-width: 480px) {
    max-width: 350px;
  }

  @media (max-width: 420px) {
    max-width: 300px;
  }

  @media (max-width: 375px) {
    max-width: 280px;
  }

  @media (max-width: 345px) {
    max-width: 250px;
  }
}

.button-close {
  position: absolute;
  right: 25px;
  display: block;
  width: 12px;
  height: 12px;
  background: url("../img/svg/close.svg") center center no-repeat;
}

