.header {
  &__icons {
    display: flex;
    align-items: center;
    gap: 1rem;
  }

  &__link-swiper {
    font-weight: 400;
    font-size: 1rem;
    color: #726e8d;
  }
}

.navbar {
  &__link {
    font-weight: 400;
    font-size: 1rem;
    color: #726e8d;
    transition: 0.2s ease-in-out;

    @include hover {
      color: #201b3f;
    }
  }
}

.for-small-screens {
  @media (min-width: 34.4375rem) {
    display: none;
  }
}

.for-large-screens {
  @media (max-width: 34.375rem) {
    display: none;
  }
}

.button {
  background-color: var(--light-grey);
  transition: 0.2s ease-in-out;

  @include hover {
    background-color: rgba($color: #4e4d93, $alpha: 0.2);
  }
}
