  &__list-swiper {
    display: flex;
    align-items: center;
    gap: 32px;

    @media (max-width: 520px) {
      position: fixed;
      top: 0;
      left: -100%;
      overflow-y: auto;
      z-index: 100;
      width: 80%;
      max-width: 23.4375rem;
      height: 100vh;
      background-color: var(--white);
      transition: opacity 0.3s, visibility 0.3s, transform 0.3s, left 0.3s;
      box-shadow: 0.625rem 0 1.4375rem -0.3125rem rgba(34, 60, 80, 0.2);

      padding: 1.25rem;
      align-items: start;
      flex-direction: column;
    }
  }

