}
.header-swiper {
  &__container {
    padding: 24px var(--container-offset);

    display: flex;
    align-items: center;

    @media (min-width: 577px) {
      justify-content: center;
    }
  }

  &__container--up {
    justify-content: space-between;
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 50px;
  }

  &__container-down {
    padding: 24px var(--container-offset);
  }
}

.header-grey {
  background-color: var(--light-grey);
}

.swiper-wrapper {
  @media (min-width: 577px) {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    gap: 44px;
    text-align: center;
  }

  @include mobile {
    flex-wrap: nowrap;
    gap: 0;
  }
}

.menu--active {
  left: 0;
}
